import { Stack } from '@mui/material'
import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useLoginRedirect } from '../../hooks/useLoginRedirect'
import { startMessagesListening, stopMessagesListening } from '../../redux/chatReducer'
import { getChatStatus } from '../../redux/selectors/chatSelectors'
import { getSidebarOpen, getSidebarWidth } from '../../redux/selectors/sidebarSelectors'
import { Messages } from './Messages'
import { MessagesForm } from './MessagesForm'

export const ChatPage: React.FC = () => {
    const status = useSelector(getChatStatus)
    const open = useSelector(getSidebarOpen)
    const sidebarWidth = useSelector(getSidebarWidth)
    const dispatch = useDispatch()
    const res = useLoginRedirect()


    useEffect(() => {
        dispatch(startMessagesListening())
        return () => {
            dispatch(stopMessagesListening())
        }
    }, [dispatch])

    if (res) return res
    return (
        <Stack sx={{ width: open ? `calc(100vw - ${sidebarWidth}px)` : '100vw' }}>
            {status === 'error' ? <div>Something went wrong. Please refresh page</div> :
                <>
                    <Messages />
                    <MessagesForm open={open} sidebarWidth={sidebarWidth} />
                </>}
        </Stack>
    )
}
