import { Formik, FormikHelpers } from 'formik';
import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { StateType } from '../../redux/redux-store';
import { getUsers, UsersFormFilter } from '../../redux/usersReducer';
import { useSearchParams } from 'react-router-dom';
import { getUsersFilter } from '../../redux/selectors/usersSelectors';
import { hasValueObjQ } from '../../api/api';
import { Autocomplete, Button, Stack, TextField } from '@mui/material';
import { getSidebarOpen, getSidebarWidth } from '../../redux/selectors/sidebarSelectors';


type FriendForm = "Yes" | "No" | "null" | 'true'

type UsersSearchFormType = {
    term: string,
    friend: FriendForm
}

const UsersSearchForm: React.FC = React.memo(() => {
    const pageSize = useSelector<StateType, number>(state => state.usersPage.pageSize)
    const filter = useSelector(getUsersFilter)
    const open = useSelector(getSidebarOpen)
    const sidebarWidth = useSelector(getSidebarWidth)
    const [query, setQuery] = useSearchParams()
    const dispatch = useDispatch()

    const onSubmit = (filter: UsersSearchFormType, { setSubmitting }: FormikHelpers<UsersSearchFormType>) => {

        const parsedFilter: UsersFormFilter = {
            term: filter.term,
            friend: filter.friend === "Yes" ? true : filter.friend === "No" ? false : null
        }

        setQuery(hasValueObjQ({ page: 1, ...parsedFilter }))
        dispatch(getUsers(1, pageSize, parsedFilter))
        setSubmitting(false)
    }

    return (
        <Formik
            enableReinitialize
            initialValues={{ term: filter.term, friend: String(filter.friend) as FriendForm }}
            onSubmit={onSubmit}
        >
            {({ isSubmitting, values, handleChange, handleSubmit, setFieldValue }) => (
                <form onSubmit={handleSubmit} style={{ marginTop: '7px' }}>
                    <Stack direction={'row'} spacing={0.5} sx={{ maxWidth: open ? `calc(100vw - ${sidebarWidth}px)` : '100vw' }}>
                        <TextField
                            type="text"
                            label='Name'
                            size='small'
                            name="term"
                            style={{ width: 110 }}
                            onChange={handleChange}
                            value={values.term || ''}
                        />
                        <Autocomplete
                            options={['Yes', 'No', '---']}
                            size='small'
                            style={{ width: 110 }}
                            onChange={(e, value) => setFieldValue('friend', value)}
                            value={values.friend === 'null' || values.friend === null ? '---' : values.friend === 'true' || values.friend === 'Yes' ? 'Yes' : 'No'}
                            renderInput={(params) => <TextField {...params} label="Only Followers" fullWidth />}
                        />
                        <Button
                            variant='outlined'
                            type="submit"
                            disabled={isSubmitting}

                        >
                            Find
                        </Button>
                    </Stack>
                </form>
            )}
        </Formik>
    )
})


export default UsersSearchForm