import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getProfile, getStatus } from "../../redux/profileReducer";
import Preloader from "../../components/Preloader";
import { useParams } from "react-router-dom";
import Profile from "./Profile";
import { useLoginRedirect } from "../../hooks/useLoginRedirect";
import { getProfileIsFetching } from "../../redux/selectors/profileSelectors";
import { getMyUserId } from "../../redux/selectors/authSelectors";


const ProfileContainer: React.FC = () => {
    const myId = useSelector(getMyUserId)
    const isFetching = useSelector(getProfileIsFetching)
    const searchId = useParams().id
    const id = searchId === 'me' ? myId : searchId
    const dispatch = useDispatch()
    const res = useLoginRedirect()
    
    
    useEffect(() => {
        dispatch(getProfile(id as number))
        dispatch(getStatus(id as number))
    }, [id, dispatch])

    if (res && searchId === 'me') return res
    return (
        <>
            {!isFetching
            ? <Profile id={searchId === 'me' && myId} />
            : <Preloader />}
        </>
    )
}

export default ProfileContainer
