import React from 'react'
import { Avatar, Box, Divider, Stack, Typography } from '@mui/material'
import ChatPreview from '../../assets/chatpreview.jpg'
import ProfilePreview from '../../assets/profilepreview.jpg'
import { makeStyles } from '@mui/styles'
import { useMatchMedia } from '../../hooks/useMatchMedia'
import { useSelector } from 'react-redux'
import { getSidebarOpen, getSidebarWidth } from '../../redux/selectors/sidebarSelectors'

const useStyles = makeStyles({
    img: {
        width: '50%',
        objectFit: 'scale-down'
    },
    deskTitle: {
        fontFamily: 'Nunito, sans-serif',
        fontWeight: 'bold'
    },
    phoneTitle: {
        fontFamily: 'Nunito, sans-serif',
        fontWeight: 'bold',
        fontSize: '18px'
    }
})

export const HomepagePage = () => {
    const styles = useStyles()
    const open = useSelector(getSidebarOpen)
    const sidebarWidth = useSelector(getSidebarWidth)

    return (
        <div style={{ minWidth: open ? `calc(96vw - ${sidebarWidth}px)` : '98vw' }}>
            <CustomBox props={{ marginTop: '-3%' }}>
                <Box>
                    <Title>
                        Create and customize your profile
                    </Title>
                    <Title>
                        View another users' profiles
                    </Title>
                </Box>
                <img src={ProfilePreview} className={styles.img} />
            </CustomBox>
            <CustomBox props={{ marginTop: '-3%' }}>
                <img src={ChatPreview} className={styles.img} />
                <Title>
                    Communicate with people in common chat
                </Title>
            </CustomBox>
            <footer>
                <Stack direction={'row'} sx={{
                    display: 'flex',
                    backgroundColor: 'primary.main',
                    height: '70px',
                    color: 'secondary.main',
                    fontSize: '18px',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    padding: '0 5px'
                }}>
                    <Typography>Copyright &copy; 2022 Kovkuto</Typography>
                    <a href='https://www.fl.ru/users/nikkovkuto/portfolio/' style={{ textDecoration: 'none' }}><Avatar variant='rounded' sx={{ marginLeft: 'auto', marginRight: 'auto' }}>FL</Avatar></a>
                </Stack>
            </footer>
        </div>
    )
}

interface IStandart {
    props?: { [Key: string]: any },
    children: React.ReactNode
}

const Title: React.FC<IStandart> = ({ children }) => {
    const styles = useStyles()
    const { phone } = useMatchMedia()


    return (
        <Typography className={phone ? styles.phoneTitle : styles.deskTitle} variant={phone ? 'h6' : 'h4'} gutterBottom>
            {children}
        </Typography>
    )
}

const CustomBox: React.FC<IStandart> = ({ props, children }) => {
    return (
        <Box
            display="flex"
            justifyContent="center"
            alignItems="center"
            minHeight="91.5vh"
            sx={{ marginLeft: 'auto', marginRight: 'auto', ...props }}
        >
            {children}
        </Box>
    )
}
