import React, { useState } from "react"
import Preloader from "../../components/Preloader"
import ProfileDataForm from "./ProfileDataForm";
import { useDispatch, useSelector } from "react-redux";
import { Button, Dialog, DialogActions, DialogTitle, Stack, Typography } from "@mui/material";
import { useMatchMedia } from "../../hooks/useMatchMedia";
import { makeStyles } from "@mui/styles";
import { Logout } from "@mui/icons-material";
import { logout } from "../../redux/authReducer";
import { getExtraAvatar } from "../../redux/selectors/authSelectors";
import { getCurrentProfile, getProfileStatus } from "../../redux/selectors/profileSelectors";
import { getSidebarOpen, getSidebarWidth } from "../../redux/selectors/sidebarSelectors";

type Props = {
    id: number | undefined | null | false
}

const useStyles = makeStyles((theme) => ({
    phoneImg: {
        marginLeft: 'auto',
        marginRight: 'auto',
        height: '124px',
        width: '124px',
        marginTop: '5vh',
        objectFit: 'contain',
        objectPosition: '50%',
    },
    deskImg: {
        marginLeft: '10%',
        maxHeight: '70vh',
        maxWidth: '350px',
        objectFit: 'scale-down',
        objectPosition: '0 0'
    },
    subInformation: {
        marginTop: '16px',
        fontFamily: 'Roboto',
        fontWeight: 'regular'
    },
    button: {
        width: '45%',
        marginBottom: '10px'
    }
}))


const Profile: React.FC<Props> = ({ id }) => {
    const [editMode, setEditMode] = useState(false)
    const [dialogOpen, setDialogOpen] = useState(false)
    const status = useSelector(getProfileStatus)
    const open = useSelector(getSidebarOpen)
    const sidebarWidth = useSelector(getSidebarWidth)
    const currentProfile = useSelector(getCurrentProfile)
    const extraAvatar = useSelector(getExtraAvatar)
    const { phone } = useMatchMedia()
    const dispatch = useDispatch()
    const styles = useStyles()

    const handleChoice = (isSure: boolean) => {
        if (isSure) {
            dispatch(logout())
        }
        setDialogOpen(false)
    }

    if (!currentProfile.userId) {
        return <Preloader />
    }
    let photo = currentProfile.photos.large
    if (photo) {
        photo = currentProfile.photos.large
    } else {
        photo = extraAvatar
    }

    let contacts = Object.keys(currentProfile.contacts).filter(key => currentProfile.contacts[key] ? key : null).map(key => {
        return (
            <Stack key={key} sx={phone ? { marginLeft: 'auto', marginRight: 'auto' } : { marginLeft: '10%' }} direction={'row'} width='auto'>
                {!phone && <>
                    <Typography sx={{ fontWeight: '420' }}>
                        {key}:
                    </Typography>
                    <a href={currentProfile.contacts[key]}>
                        <Typography sx={{ marginLeft: '3px' }}>
                            {currentProfile.contacts[key]}
                        </Typography>
                    </a>
                </>}
                {phone && <>
                    <a href={currentProfile.contacts[key]}>
                        <Typography sx={{ fontWeight: '420' }}>
                            {key}
                        </Typography>
                    </a>
                </>}
            </Stack>
        )
    })


    return (
        <Stack direction={phone ? 'column' : 'row'} spacing={8} sx={{ ...phone ? { textAlign: 'center', width: open ? `calc(100vw - ${sidebarWidth}px)` : '100vw' } : { marginTop: '4%', marginLeft: 'auto', marginRight: 'auto' } }}>
            <img className={phone ? styles.phoneImg : styles.deskImg} src={photo as string | undefined} alt='avatar' />
            <Stack>
                {editMode ? <ProfileDataForm editMode={editMode} setEditMode={setEditMode} /> :
                    <Stack direction={'column'} sx={{ listStyle: 'none', ...phone ? { textAlign: 'center', } : {} }} spacing={phone ? 0 : 0.8}>
                        <Typography style={{ fontFamily: 'Roboto, sans-serif', fontWeight: '700', fontSize: '32px' }}>
                            {currentProfile.fullName}
                        </Typography>
                        <Typography style={{ fontFamily: 'Roboto, sans-serif', fontWeight: '500', fontSize: '20px', marginTop: '16px' }}>
                            {status}
                        </Typography>

                        {currentProfile.aboutMe && <Typography className={styles.subInformation}>
                            About Me: {currentProfile.aboutMe}
                        </Typography>}
                        <Typography className={styles.subInformation}>
                            Looking for a job: {currentProfile.lookingForAJob ? "Yes" : "No"}
                        </Typography>
                        {currentProfile.lookingForAJobDescription && <Typography className={styles.subInformation}>
                            Skills: {currentProfile.lookingForAJobDescription}
                        </Typography>}


                        {contacts.length === 0 ? <Typography className={styles.subInformation}>
                            "Contacts: Missing"
                        </Typography> : <>Contacts:
                            <Stack>{contacts}</Stack>
                        </>}



                    </Stack>
                }
                <Stack spacing={1} sx={{ marginTop: '10px', ...phone ? { margin: 'auto' } : {} }}>
                    {id && !editMode && <Button
                        onClick={() => setEditMode(!editMode)}
                        variant='contained'
                        size="small"
                        disableElevation
                        className={phone ? '' : styles.button}
                    >Edit</Button>}
                    {id && !editMode && <Button
                        variant='contained'
                        size="small"
                        disableElevation className={phone ? '' : styles.button}
                        onClick={() => setDialogOpen(true)}
                        endIcon={<Logout />}
                    >
                        Logout
                    </Button>}
                    <LogoutDialog dialogOpen={dialogOpen} handleChoice={handleChoice} />
                </Stack>
            </Stack>
        </Stack>
    )
}

const LogoutDialog: React.FC<{ dialogOpen: boolean, handleChoice: Function }> = ({ dialogOpen, handleChoice }) => {
    return (
        <Dialog
            open={dialogOpen}
            onClose={() => handleChoice(false)}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">
                {"Are you sure you will be logged out?"}
            </DialogTitle>
            <DialogActions>
                <Button onClick={() => handleChoice(false)}>Cancel</Button>
                <Button onClick={() => handleChoice(true)} autoFocus>
                    Confirm
                </Button>
            </DialogActions>
        </Dialog>
    )
}

export default Profile