import { createSelector } from 'reselect';
import { StateType } from './../redux-store';

const openSelector = (state: StateType) => state.sidebar.open
const sidebarWidthSelector = (state: StateType) => state.sidebar.sidebarWidth
const titlesSelector = (state: StateType) => state.sidebar.titles


export const getSidebarOpen = createSelector(openSelector, open => open)
export const getSidebarWidth = createSelector(sidebarWidthSelector, width => width)
export const getTitles = createSelector(titlesSelector, titles => titles)
