import { Avatar, Box, Stack, Theme, Typography, useTheme } from "@mui/material"
import { makeStyles } from "@mui/styles"
import React, { UIEvent, useEffect, useRef, useState } from "react"
import { useSelector } from "react-redux"
import { Link } from "react-router-dom"
import { ChatMessage } from "../../redux/chatReducer"
import { getExtraAvatar, getMyUserId } from "../../redux/selectors/authSelectors"
import { getChatMessages } from "../../redux/selectors/chatSelectors"

const useStyles = makeStyles({
    name: {
        fontFamily: 'Nunito, sans-serif',
        fontWeight: 'bold',
        fontSize: '18px',
    },
    message: {
        fontFamily: 'Nunito, sans-serif',
        fontWeight: 'Regular',
        fontSize: '16px',
    },
    scrollbar: {
        height: "80vh",
        overflowY: "auto",
        backgroundColor: "white",
        borderRadius: "3px",
        '&::-webkit-scrollbar': {
            width: '0.7em'
        },
        '&::-webkit-scrollbar-track': {
            backgroundColor: 'transparetnt'
        },
        '&::-webkit-scrollbar-thumb': {
            backgroundColor: (theme: Theme) => theme.palette.primary.light,
            borderRadius: '10px'
        }
    }
})

export const Messages: React.FC = () => {
    const messages = useSelector(getChatMessages)
    const [isAutoScroll, setIsAutoScroll] = useState(true)
    const messagesAnchorRef = useRef<HTMLDivElement>(null)
    const theme = useTheme()
    const styles = useStyles(theme)


    useEffect(() => {
        if (isAutoScroll) messagesAnchorRef.current?.scrollIntoView({ behavior: "smooth" })
    }, [messages, isAutoScroll])

    const scrollHandler = (e: UIEvent<HTMLDivElement>) => {
        const el = e.currentTarget
        if (el.scrollHeight - el.scrollTop === el.clientHeight) {
            if (!isAutoScroll) setIsAutoScroll(true)
        } else {
            if (isAutoScroll) setIsAutoScroll(false)
        }
    }

    return (
        <Stack className={styles.scrollbar} onScroll={scrollHandler}>
            {messages.map((message: ChatMessage) => <Message key={message.messageId} {...message} />)}
            <div ref={messagesAnchorRef}></div>
        </Stack>
    )
}

const Message: React.FC<ChatMessage> = React.memo(({ userName, message, photo, userId }) => {
    const myId = useSelector(getMyUserId)
    const extraAvatar = useSelector(getExtraAvatar)
    const theme = useTheme()
    const styles = useStyles(theme)
    const isMe = userId === myId

    return (
        <Stack sx={{ ...isMe ? { justifyContent: 'space-between', marginRight: '5px' } : { marginLeft: '10px' } }} direction={'row'}>
            <Box></Box>
            <Stack direction='row' sx={{ padding: "3px" }}>
                {!isMe &&
                    <Link to={`../profile/${userId}`}>
                        <Avatar src={photo || extraAvatar} alt="ava" />
                    </Link>
                }
                <Box sx={{ backgroundColor: !isMe ? '#E2FFDE' : '#E4EFFF', padding: '5px 15px 5px 7px', borderRadius: '3px' }}>
                    {!isMe && <Typography className={styles.name}>{userName}</Typography>}
                    <Typography className={styles.message}>{message}</Typography>
                </Box>
            </Stack>
        </Stack>
    )
})