import { BaseThunk, actions } from './redux-store';
import { profileAPI } from './../api/profileAPI';
import { PayloadAction } from '@reduxjs/toolkit';
import { IPhotos } from './profileReducer';
import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    avatar: null as string | null
}

const headerSLice = createSlice({
    name: 'headerReducer',
    initialState: initialState,
    reducers: {
        setAvatar: (state, action: PayloadAction<string | null>) => {
            state.avatar = action.payload
        }
    }
})

type Thunk = BaseThunk<actions<typeof headerSLice.actions>>

const {setAvatar} = headerSLice.actions

export const getMyAvatar = (): Thunk => async (dispatch, getState) => {
    if (getState().auth.isAuth) {
        try {
            const data = await profileAPI.getProfile(getState().auth.userId as number)
            dispatch(setAvatar(data.photos.large))
        } catch (err) {
            console.log(err);
        }
    }
}

export default headerSLice.reducer
