import { Box, Button, Input, Stack, Typography } from "@mui/material"
import { makeStyles } from "@mui/styles"
import { ChangeEvent, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { sendMessage } from "../../redux/chatReducer"
import { getChatStatus } from "../../redux/selectors/chatSelectors"

const useStyles = makeStyles((theme) => ({
    name: {
        fontFamily: 'Nunito, sans-serif',
        fontWeight: 'bold',
        fontSize: '18px',
    }
}))


export const MessagesForm: React.FC<{ open: boolean, sidebarWidth: number }> = ({ open, sidebarWidth }) => {
    const [message, setMessage] = useState("")
    const status = useSelector(getChatStatus)
    const styles = useStyles()
    const dispatch = useDispatch()


    const sendOnClick = () => {
        dispatch(sendMessage(message))
        setMessage('')
    }

    return (
        <Stack spacing={1} sx={{ width: open ? `calc(98vw - ${sidebarWidth}px)` : '98vw', margin: '0 auto' }}>
            <Box sx={{ height: '5px', backgroundColor: '#616161' }}></Box>
            <Input
                autoComplete='off'
                placeholder='Write your message . . .'
                disableUnderline
                multiline
                rows={3}
                value={message}
                onChange={(e: ChangeEvent<HTMLInputElement>) => {
                    setMessage(e.currentTarget.value)
                }}
                endAdornment={<Button
                    onClick={sendOnClick}
                    disabled={status !== 'ready'}
                    variant='contained'
                    size='small'
                    sx={{
                        width: '5%',
                        maxWidth: '5%',
                    }}
                >
                    <Typography className={styles.name}>
                        Send
                    </Typography>
                </Button>
                }
            />
        </Stack>
    )
}
