import { Menu } from "@mui/icons-material";
import { AppBar, Avatar, Box, Button, IconButton, Stack, Toolbar, Typography } from "@mui/material";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { getMyAvatar } from "../redux/headerReducer";
import { getIsAuth, getLogin } from "../redux/selectors/authSelectors";
import { getHeaderAvatar } from "../redux/selectors/headerSelectors";
import { getSidebarOpen } from "../redux/selectors/sidebarSelectors";
import { setOpen } from "../redux/sidebarReducer";


const Header: React.FC = () => {
    const isAuth = useSelector(getIsAuth)
    const login = useSelector(getLogin)
    const open = useSelector(getSidebarOpen)
    const avatar = useSelector(getHeaderAvatar)
    const dispatch = useDispatch()
    useEffect(() => {
        dispatch(getMyAvatar())
    }, [dispatch])


    return (
        <>
            <AppBar position='relative'><Toolbar sx={{ height: '6.5vh' }}></Toolbar></AppBar>

            <AppBar position='fixed' sx={{
                zIndex: (theme) => theme.zIndex.drawer + 1,
                backgroundColor: 'primary.main'
            }}>
                <Toolbar sx={{ height: '6.5vh', justifyContent: 'space-between' }}>
                    <Stack direction={'row'}>
                    <IconButton
                        color="inherit"
                        aria-label="open sidebar"
                        onClick={() => dispatch(setOpen(!open))}
                        edge="start"
                    >
                        <Menu />
                    </IconButton>

                    <Link to={'/'} style={{ textDecoration: 'none', color: 'inherit', marginTop: '3px' }}>
                        <Typography variant="h6">
                            Messanger
                        </Typography>
                    </Link>
                    </Stack>

                    <Box sx={{ float: 'right' }}>
                        {isAuth ?
                            <Link to='profile/me' style={{ textDecoration: 'none', color: '#EFEFEF' }}>
                                <Stack direction={'row'} >
                                    <Typography variant="subtitle1" sx={{ marginRight: '5px' }}>{login}</Typography>
                                    <Avatar sx={{ width: 24, height: 24 }} src={avatar ? avatar : 'https://miro.medium.com/max/720/1*W35QUSvGpcLuxPo3SRTH4w.png'} />
                                </Stack>
                            </Link> : <Link to='/login' style={{ textDecoration: 'none' }}>
                                <Button sx={{ color: '#EFEFEF' }} variant='text'>
                                    Login
                                </Button>
                            </Link>
                        }
                    </Box>
                </Toolbar>
            </AppBar>
        </>
    )
}


export default Header
