import React, { ComponentClass } from 'react'

export const groupFieldControl = (Component: React.FC | ComponentClass, groupProps: any, values: {[Key: string]: any}, labelOrPlaceholder: 'placeholder' | 'label', className?: any) => 
(name: string, description?: string, props?: any, curtomValueName?: string) => {
  return (
    <Component {...groupProps} {...props} value={values[curtomValueName ? curtomValueName : name]} className={className} name={name} {...{[labelOrPlaceholder]: description}}/>
  )
}
