import { StateType } from './../redux-store';
import { createSelector } from 'reselect';

const isFetchingSelector = (state: StateType) => state.profilePage.isFetching
const statusSelector = (state: StateType) => state.profilePage.status
const currentProfileSelector = (state: StateType) => state.profilePage.currentProfile

export const getProfileIsFetching = createSelector(isFetchingSelector, isFetching => isFetching)
export const getProfileStatus = createSelector(statusSelector, status => status)
export const getCurrentProfile = createSelector(currentProfileSelector, currentProfile => currentProfile)
