import React, { useState } from 'react';
import { Formik } from 'formik';
import { Button, Checkbox, FormControlLabel, IconButton, Input, Stack, Typography } from '@mui/material';
import { Box } from '@mui/system';
import * as Yup from 'yup';
import { useDispatch, useSelector } from 'react-redux';
import { login } from '../redux/authReducer';
import { Navigate } from 'react-router-dom';
import { Visibility, VisibilityOff } from '@mui/icons-material';
import { getIsAuth, getAuthErrors, getCaptchaUrl } from '../redux/selectors/authSelectors';

const LoginShema = Yup.object().shape({
    email: Yup.string().email('Invalid Email').required('Requiered'),
    password: Yup.string().min(8, '8 symbol is minimum').required('Requiered'),
    rememberMe: Yup.boolean()
})

const Login = () => {
    const [visibility, setVisibility] = useState(false)
    const isAuth = useSelector(getIsAuth)
    const loginErrors = useSelector(getAuthErrors)
    const captchaUrl = useSelector(getCaptchaUrl)
    const dispatch = useDispatch()


    if (isAuth) return <Navigate to="../profile/me" />
    return (
        <Box sx={{ margin: 'auto' }}>
            <Formik
                initialValues={{ email: '', password: '', rememberMe: false, captcha: '' }}
                validationSchema={LoginShema}
                onSubmit={(values, { setSubmitting }) => {
                    dispatch(login(values.email, values.password, values.rememberMe, values.captcha))
                    setSubmitting(false)
                }}
            >
                {({
                    values,
                    errors,
                    touched,
                    handleChange,
                    handleBlur,
                    handleSubmit,
                    isSubmitting,
                    setFieldValue
                }) => (
                    <form onSubmit={handleSubmit}>
                        <Stack direction='column' spacing={1} sx={{ marginTop: '30vh'}}>
                            <Input
                                placeholder='Email'
                                type='email'
                                name='email'
                                size='small'
                                onChange={handleChange}
                                onBlur={handleBlur}
                                value={values.email}
                            />
                            <Typography sx={{ width: '100%', color: '#d90429' }}>{errors.email && touched.email && errors.email}</Typography>
                            <Input
                                autoComplete='off'
                                placeholder='Password'
                                type={visibility ? 'text' : 'password'}
                                name='password'
                                size='small'
                                endAdornment={<IconButton onClick={() => setVisibility((vis) => !vis)}>{visibility ? <Visibility /> : <VisibilityOff />}</IconButton>}
                                sx={{ width: '100%' }}
                                onChange={handleChange}
                                onBlur={handleBlur}
                                value={values.password}
                            />
                            <Typography sx={{ width: '100%', color: '#d90429' }}>{errors.password && touched.password && errors.password}</Typography>
                            <FormControlLabel
                                label="Remember Me"
                                checked={values.rememberMe}
                                onChange={() => setFieldValue("rememberMe", !values.rememberMe)}
                                control={<Checkbox
                                    size='small'
                                />}
                            />
                            {captchaUrl && <>
                                <img src={captchaUrl} />
                                {<Input
                                    placeholder='Captcha'
                                    name='captcha'
                                    size='small'
                                    sx={{ width: '100%' }}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    value={values.captcha}
                                />}
                            </>}
                            <Button
                                variant='contained'
                                sx={{
                                    backgroundColor: '#223349',
                                    '&:hover': { backgroundColor: '#2A4263' }
                                }}
                                type="submit"
                                disabled={isSubmitting}>
                                Login
                            </Button>
                            <Typography sx={{ textAlign: 'center', color: '#d90429' }}>{loginErrors[0]}</Typography>
                        </Stack>
                    </form>
                )}
            </Formik>
        </Box>
    )
};

export default Login;