import * as React from 'react';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import { Stack, Typography } from '@mui/material';
import { ThemeProvider, createTheme } from '@mui/material/styles'
import { useDispatch, useSelector } from 'react-redux';
import { StateType } from './redux/redux-store';
import { Route, Routes } from 'react-router-dom';
import Header from './components/Header';
import Navbar from './components/Navbar';
import UsersPage from './pages/Users/UsersPage';
import ProfilePage from './pages/Profile/ProfilePage';
import { ChatPage } from './pages/Chat/ChatPage';
import Login from './features/Login';
import { useMatchMedia } from './hooks/useMatchMedia';
import { useEffect } from 'react';
import { setsidebarWidth } from './redux/sidebarReducer';
import { initialzeApp } from './redux/appReducer';
import { getSidebarOpen, getSidebarWidth } from './redux/selectors/sidebarSelectors';
import { HomepagePage } from './pages/Homepage/HomepagePage';

const theme = createTheme({
    palette: {
        primary: {
            light: '#256D85',
            main: '#2B4865',
            dark: '#002B5B'
        },
        secondary: {
            main: '#F9F5EB'
        }
    },
    components: {
        MuiCssBaseline: {
            styleOverrides: (themeParam) => ({
                body: {
                    '&::-webkit-scrollbar': {
                        width: '0.7em'
                    },
                    '&::-webkit-scrollbar-track': {
                        backgroundColor: 'transparetnt'
                    },
                    '&::-webkit-scrollbar-thumb': {
                        backgroundColor: themeParam.palette.primary.light,
                        borderRadius: '10px'
                    }
                }
            }),
        },
    },
})

const App = React.memo(() => {
    const open = useSelector(getSidebarOpen)
    const initialized = useSelector((state: StateType) => state.app.initialized)
    const sidebarWidth = useSelector(getSidebarWidth)
    const { phone } = useMatchMedia()
    const dispatch = useDispatch()
    React.useLayoutEffect(() => {
        if (phone) {
            dispatch(setsidebarWidth(140))
        } else {
            dispatch(setsidebarWidth(240))
        }
    }, [phone])
    useEffect(() => { dispatch(initialzeApp()) }, [])

    if (!initialized) return <Loading />
    return (
        <ThemeProvider theme={theme}>
            <Stack>
                <CssBaseline />
                <Header />
                <Navbar />

                <Box sx={{
                    marginTop: '1%',
                    marginLeft: open ? sidebarWidth + 'px' : -0,
                    transition: 'width 2s',
                    display: 'flex'
                }}>
                    <React.Suspense fallback={<Loading />}>
                        <Routes>
                            <Route path='/' element={<HomepagePage />} />
                            <Route path="/profile/:id" element={<ProfilePage />} />
                            <Route path="/find-users" element={<UsersPage />} />
                            <Route path="/chat" element={<ChatPage />} />
                            <Route path="/login" element={<Login />} />
                            <Route path='*' element={<div>Error 404</div>} />
                        </Routes>
                    </React.Suspense>
                </Box>
            </Stack>
        </ThemeProvider>
    );
})

const Loading = () => {
    return <Typography variant='h2' sx={{ textAlign: 'center', marginTop: 'auto', marginBottom: 'auto' }}>Loading . . . </Typography>
}

export default App