import React, { useEffect } from "react";
import User from "./User"
import Preloader from "../../components/Preloader";
import { getUsers, setCurrentPage } from "../../redux/usersReducer";
import { getCurrentPage, getUsersIsFetching, getPageSize, getTotalUsersCount, getUsersData, getUsersFilter } from "../../redux/selectors/usersSelectors";
import { useDispatch, useSelector } from "react-redux";
import Paginator from "../../components/Paginator";
import UsersSearchForm from './UsersSearchForm';
import { useSearchParams } from 'react-router-dom';
import { hasValueObjQ } from '../../api/api';
import { Box, Stack } from "@mui/material";
import { getSidebarOpen, getSidebarWidth } from "../../redux/selectors/sidebarSelectors";

// ---------------------------Component--------------------------- \\
const Users: React.FC = React.memo(() => {

    // --------------------------Selctors-------------------------- \\
    const open = useSelector(getSidebarOpen)
    const sidebarWidth = useSelector(getSidebarWidth)
    const totalCount = useSelector(getTotalUsersCount)
    const pageSize = useSelector(getPageSize)
    const usersData = useSelector(getUsersData)
    const isFetching = useSelector(getUsersIsFetching)
    const filter = useSelector(getUsersFilter)
    const currentPage = useSelector(getCurrentPage)

    // -----------------------WorkSpace----------------------- \\
    const dispatch = useDispatch()

    const [query, setQuery] = useSearchParams()

    const friend = query.get("friend") === "true" ? true : query.get("friend") === "false" ? false : null
    const parsedFilter = {
        term: query.get("term") as string,
        friend: friend
    }

    useEffect(() => {
        setQuery(hasValueObjQ({ page: currentPage, ...filter }))
        dispatch(getUsers(currentPage ? currentPage : 1, pageSize, parsedFilter))
    }, [currentPage, pageSize])

    useEffect(() => {
        setQuery(hasValueObjQ({ page: query.get("page"), ...parsedFilter }))
        dispatch(setCurrentPage(isNaN(Number(query.get("page"))) ? currentPage ? currentPage : 1 : Number(query.get("page"))))
    }, [])

    let usersItems = usersData.map(user => {
        return <User
            key={user.id}
            {...user}
        />
    })

    // ----------------------------JSX---------------------------- \\
    return (
        <Stack sx={{ width: open ? `calc(100vw - ${sidebarWidth}px)` : '100vw' }}>
            <Box sx={{ margin: '0 auto' }}>
                <Paginator
                    pageSize={pageSize}
                    currentPage={currentPage}
                    setCurrentPage={(pageNumber: number) => dispatch(setCurrentPage(pageNumber))} totalCount={totalCount}
                />
            </Box>
            {isFetching ? <Preloader /> : null}
            <Box style={{ marginLeft: 'auto', marginRight: 'auto' }}><UsersSearchForm /></Box>
            <Box style={{ alignItems: "center" }}>{usersItems}</Box>
        </Stack>
    )
})

export default Users
