import { createSelector } from 'reselect';
import { StateType } from './../redux-store';


const isAuth = (state: StateType) => state.auth.isAuth
const login = (state: StateType) => state.auth.login
const userId = (state: StateType) => state.auth.userId
const extraAvatar = (state: StateType) => state.auth.extraAvatar
const captchaUrl = (state: StateType) => state.auth.captchaUrl
const errors = (state: StateType) => state.auth.errors


export const getIsAuth = createSelector(isAuth, isAuth => isAuth)
export const getLogin = createSelector(login, login => login)
export const getMyUserId = createSelector(userId, id => id)
export const getExtraAvatar = createSelector(extraAvatar, extraAvatar => extraAvatar)
export const getCaptchaUrl = createSelector(captchaUrl, captchaUrl => captchaUrl)
export const getAuthErrors = createSelector(errors, errors => errors)

