import { useEffect, useLayoutEffect } from "react"
import { useState } from "react"

export const useMatchMedia = () => {
    const [matchSizes, setMatchSizes] = useState([false, false, false])

    const sizes = ['1200', '750', '350']
    const sizesMatches = sizes.map((size) => {
        return matchMedia(`(min-width: ${size}px)`).matches
    })

    useLayoutEffect(() => {
        let newMatchSizes = [] as boolean[]
        sizes.map((size, index) => {
            newMatchSizes[index] = matchMedia(`(min-width: ${size}px)`).matches
        })
        setMatchSizes(newMatchSizes)
    }, [...sizesMatches])

    return {
        desktop: matchSizes[0],
        tablet: matchSizes[0] ? false : true,
        phone: matchSizes[1] ? false : true,
    }
}
