import React from "react";
import { useSelector } from "react-redux";
import { Drawer, List, ListItem, ListItemButton, ListItemIcon, ListItemText } from "@mui/material";
import { NavLink } from "react-router-dom";
import { Chat, Person, PersonSearch } from "@mui/icons-material";
import { getSidebarOpen, getSidebarWidth, getTitles } from "../redux/selectors/sidebarSelectors";


function Navbar(){
    const sidebarWidth = useSelector(getSidebarWidth)
    const navBarTitles = useSelector(getTitles)
    const open = useSelector(getSidebarOpen)

    return(
        <Drawer
                variant="persistent"
                anchor="left"
                open={open}
                PaperProps={{
                    sx: { width: sidebarWidth, backgroundColor: 'secondary.main' },
                }}
            >
                <List>
                    <ListItem disablePadding sx={{ minHeight: '8vh' }} />
                    {navBarTitles.map((title, indexT) => (
                        <NavLink to={title.url} style={{
                            textDecoration: 'none',
                            color: 'black'
                        }} key={title.id}>
                            <ListItem disablePadding>
                                <ListItemButton>
                                    <ListItemIcon>
                                        {[<Person key={'person'} />, <Chat key={'chat'} />, <PersonSearch key={'personSearch'} />].filter((el, indexI) => indexI === indexT)}
                                    </ListItemIcon>
                                    <ListItemText primary={title.text} />
                                </ListItemButton>
                            </ListItem>
                        </NavLink>
                    ))}
                </List>
            </Drawer>
    )
}


export default Navbar
