import React, { ChangeEvent } from 'react';
import { Formik } from 'formik';

import { setPhoto, setProfile, updateStatus } from '../../redux/profileReducer';
import { Accordion, AccordionDetails, AccordionSummary, Box, Button, Checkbox, FormControlLabel, Stack, TextField, Typography } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { ArrowBack, ExpandMore } from '@mui/icons-material';
import { groupFieldControl } from '../../components/fieldControl';
import { makeStyles } from '@mui/styles';
import { getMyUserId } from '../../redux/selectors/authSelectors';
import { getCurrentProfile, getProfileStatus } from '../../redux/selectors/profileSelectors';

interface IProfileDataForm {
    editMode: boolean
    setEditMode: Function
}

const useStyles = makeStyles((theme) => ({
    formControl: {
        width: '100%'
    }
}))

const ProfileDataForm: React.FC<IProfileDataForm> = ({ editMode, setEditMode }) => {
    const currentProfile = useSelector(getCurrentProfile)
    const userId = useSelector(getMyUserId)
    const status = useSelector(getProfileStatus)
    const styles = useStyles()
    const dispatch = useDispatch()

    const onSetPhotoSelected = (e: ChangeEvent<HTMLInputElement>) => {
        if (e.target.files && e.target.files.length) {
            dispatch(setPhoto(e.target.files[0]))
        }
    }

    return (
        <Box>
            <Formik
                initialValues={{
                    ...currentProfile,
                    contacts: currentProfile.contacts,
                    newStatus: status
                }}
                onSubmit={({ fullName, newStatus, aboutMe, lookingForAJob, lookingForAJobDescription, contacts }) => {
                    dispatch(setProfile({
                        userId: userId as number,
                        fullName,
                        aboutMe,
                        lookingForAJob,
                        lookingForAJobDescription,
                        contacts,
                        photos: currentProfile.photos
                    }))
                    if (status !== newStatus) {
                        dispatch(updateStatus(newStatus))
                    }
                }}
            >
                {({
                    values,
                    handleChange,
                    handleBlur,
                    handleSubmit,
                    isSubmitting,
                    setFieldValue
                    /* and other goodies */
                }) => {
                    const createField = groupFieldControl(TextField, { onChange: handleChange, onBlur: handleBlur, size: 'small' }, values, 'label', styles.formControl)
                    let contacts = Object.keys(currentProfile.contacts).map(key => {
                        return <div key={key}>{createField(`contacts.${key}`, key, { defaultValue: currentProfile.contacts[key] })}</div>
                    })

                    return (
                        <form onSubmit={handleSubmit}>
                            <Stack width={'150%'}>
                                <Stack direction={'column'} spacing={1}>
                                    {createField('fullName', 'Full Name')}
                                    {createField('newStatus', 'Status')}
                                    {createField('aboutMe', 'About Me')}
                                    <FormControlLabel
                                        label="Looking For A Job"
                                        checked={values.lookingForAJob}
                                        onChange={() => setFieldValue("lookingForAJob", !values.lookingForAJob)}
                                        control={<Checkbox
                                            size='small'
                                        />}
                                    />
                                    {createField('lookingForAJobDescription', 'Skills')}
                                    <Accordion elevation={0} sx={{
                                        "&.MuiAccordion-root:before": {
                                            backgroundColor: "transparent"
                                        }
                                    }} disableGutters>
                                        <AccordionSummary
                                            expandIcon={<ExpandMore />}
                                            aria-controls="panel1a-content"
                                            id="panel1a-header"
                                        >
                                            <Typography>Contacts</Typography>
                                        </AccordionSummary>
                                        <AccordionDetails>
                                            <Stack direction='column' spacing={1}>
                                                {contacts}
                                            </Stack>
                                        </AccordionDetails>
                                    </Accordion>
                                </Stack>

                                <Stack spacing={1} sx={{ width: '100%' }}>
                                    <Button
                                        variant="contained"
                                        component="label"
                                        sx={{
                                            backgroundColor: '#223349',
                                            '&:hover': { backgroundColor: '#2A4263' },
                                        }}
                                    >
                                        Update Photo
                                        <input
                                            type="file"
                                            onChange={onSetPhotoSelected}
                                            hidden
                                        />
                                    </Button>
                                    <Button sx={{
                                        backgroundColor: '#223349',
                                        '&:hover': { backgroundColor: '#2A4263' },
                                    }} variant='contained' type="submit" disabled={isSubmitting}>
                                        Save
                                    </Button>
                                    <Button
                                        onClick={() => setEditMode(!editMode)}
                                        variant='contained'
                                        disableElevation
                                        sx={{
                                            backgroundColor: '#223349',
                                            '&:hover': { backgroundColor: '#2A4263' },
                                        }}
                                        startIcon={<ArrowBack />}
                                    >Back</Button>
                                </Stack>
                            </Stack>
                        </form>
                    )
                }}
            </Formik>
        </Box>
    )
}

export default ProfileDataForm