import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { Avatar, Theme } from '@mui/material';
import { makeStyles } from '@mui/styles';
import { IPhotos } from '../../redux/profileReducer';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { followUnfollowFlow } from '../../redux/usersReducer';
import { getFollowInProgress } from '../../redux/selectors/usersSelectors';
import { getExtraAvatar } from '../../redux/selectors/authSelectors';
import { useTheme } from '@mui/material';

const useStyles = makeStyles({
  avatar: {
    width: '70px', 
    height: '70px',
    marginLeft: 'auto',
    marginRight: 'auto',
    marginTop: '10px'
  },
  button: {
    marginLeft: 'auto',
    marginRight: 'auto'
  },
  card: {
    maxWidth: 305,
    marginLeft: 'auto',
    marginRight: 'auto',
    marginTop: '10px',
    height: 210,
    backgroundColor: (theme: Theme) => theme.palette.secondary.main
  }
})

const User: React.FC<Props> = ({id, photos, followed, name, status}) => {
  const followInProgress = useSelector(getFollowInProgress)
  const extraAvatar = useSelector(getExtraAvatar)
  const theme = useTheme()
  const styles = useStyles(theme)
  const dispatch = useDispatch()

  return (
    <Card className={styles.card} elevation={5}>
      <Link to={`../profile/${id}`}>
        <Avatar className={styles.avatar} src={photos.small ? photos.small : extraAvatar}/>
      </Link>
      <CardContent sx={{textAlign: 'center'}}>
        <Typography gutterBottom variant="h5" component="div">
          {name}
        </Typography>
        {status && <Typography variant="body2" color="text.secondary">
          {status}
        </Typography>}
      </CardContent>
      <CardActions sx={{marginTop: '-10px'}}>
        <Button
          className={styles.button} 
          size="small"
          variant='outlined'
          disabled={followInProgress.some(uId => uId === id)}
          onClick={() => dispatch(followUnfollowFlow(id, !followed))}
        >{followed ? 'Unfollow' : 'Follow'}</Button>
      </CardActions>
    </Card>
  );
}

export default User

interface Props {
  id: number,
  photos: IPhotos,
  followed: boolean,
  name: string,
  status: string 
}